import { position } from "../../common/interface";
import { reactive } from "vue";
import { THEME_STYLE_E } from "../../common/config";
import duiService from "../../services/duiService";

export let inputState: Function = (props: any): object => {
    let state = reactive({
        focused: false, // 
        onFocus: false, // 
        onOver: false, // 
        showHistory: false,
        history: false,
        disabled: false,
        error: false,
        viewable: false, // password
        icon: "unviewable",
        historyPosition: "bottom",
        cLevel: THEME_STYLE_E.PRIMARY,
    });

    let showTimer: number;
    let closeTimer: number;

    let showHistoryFn = (e: Event, inputBody: HTMLElement, historyLength: number) => {
        let viewHight: number = window.innerHeight;
        let elPosition: position = duiService.getElementViewPosition(e);
        let inputSize: position = duiService.getElementSize(inputBody);

        if (historyLength === 0) {
            return ;
        }

        if (viewHight - elPosition.y - inputSize.y - (historyLength + 1) * 22 - 10 <= 0) {
            state.historyPosition = "top";
        } else {
            state.historyPosition = "bottom";
        }

        clearTimeout(closeTimer);
        showTimer = setTimeout(() => {
            state.showHistory = true;
        }, 150);
    };

    let closeHistoryFn = () => {
        clearTimeout(showTimer);
        closeTimer = setTimeout(() => {
            state.showHistory = false;
        }, 150);
    };

    let mouseoverFn = () => {
        state.onOver = true;
    };

    let mouseleaveFn = () => {
        state.onOver = false;
    };

    let iconClickFn = () => {
        if (state.disabled) {
            return;
        }
        state.viewable = !state.viewable;
        state.icon = state.viewable ? "viewable" : "unviewable";
    };

    return {
        state,
        showHistoryFn,
        closeHistoryFn,
        mouseoverFn,
        mouseleaveFn,
        iconClickFn,
    };
};

export default {
    inputState
}