
import { defineComponent, toRefs, onMounted, watch } from "vue";
import { loadingState } from "./loading.data";
import { DECK_SIZE_E } from "../../common/config";
import Deck from "../deck/deck.vue";

/**
 * loading
 * global
 * namemaskID
 * show
 * closeCbk
 */
export default defineComponent({
    name: "loading",
    components: { Deck },
    computed: {
        propClass(): string[] {
            let propClass: string[] = [];

            if (this.global === true || this.global === "true") {
                propClass.push("global");
            }

            if (this.state.openDialog) {
                propClass.push("open");
            }

            if (this.state.closeDialog) {
                propClass.push("close");
            }

            return propClass;
        },
    },
    props: {
        global: {
            type: [Boolean, String],
            default: false,
        },
        name: {
            type: String,
            default: "",
        },
        show: {
            type: [Boolean, String],
            default: false,
        },
        closeCbk: {
            type: Function,
            default: () => {},
        },
    },
    setup(props: any, context: any) {
        let { maskId, maskUrl, state, openPop, closePop } = loadingState(
            props,
            context
        );
        let { show, closeCbk } = toRefs(props);

        if (show.value === true || show.value === "true") {
            openPop();
        }

        onMounted(() => {
            state.inited = true;
        });

        watch(show, (newVal: boolean | string) => {
            if (newVal === true || newVal === "true") {
                openPop();
            } else if (state.inited) {
                closePop(closeCbk.value);
            }
        });

        return {
            ...toRefs(props),
            state,
            openPop,
            closePop,
            maskId,
            maskUrl,
            size: DECK_SIZE_E.SMALL,
        };
    },
});
