<template v-cloak>
    <div class="deck-content">
        <div :class="['deck', propClass]">
            <slot></slot>
        </div>
    </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import { DECK_SIZE_E, THEME_STYLE_E } from "../../common/config";
import _ from "../../common/utils";

export default defineComponent({
    name: "deck",
    inheritAttrs: true,
    props: {
        size: {
            type: String,
            default: DECK_SIZE_E.LARGE,
            validator(arg: string) {
                let res = _.some(
                    DECK_SIZE_E,
                    (size: string) => size === arg.toLowerCase()
                );
                return res;
            },
        },
        type: {
            type: String,
            default: THEME_STYLE_E.PRIMARY,
            validator(arg: string) {
                return _.some(
                    THEME_STYLE_E,
                    (type: string) => type === arg.toLowerCase()
                );
            },
        },
    },
    computed: {
        propClass() {
            let propClass: any = {};
            propClass[this.size.toLowerCase()] = true;
            propClass[this.type.toLowerCase()] = true;

            return propClass;
        },
    },
});
</script>