<template v-cloak>
    <div
        class="innerdialog"
        :class="{ open: state.openDialog, close: state.closeDialog }"
    >
        <!--  -->
        <div class="backdrop" @click.self="escClickFn"></div>
        <deck class="docker" ref="docker" :type="state.cLevel" size="large">
            <!-- title -->
            <div class="title">
                <div class="title-context" auto-tips>
                    <slot name="title">{{ title }}</slot>
                </div>
                <dbutton
                    @click="closeFn"
                    icon="close"
                    border="none"
                    circle="true"
                    plgin="true"
                    :type="state.cLevel"
                    :disabled="closeDisabled"
                    v-show="closeShow"
                ></dbutton>
                <!-- <icon v-show="showCloseIcon" type="close"></icon> -->
            </div>
            <!-- body -->
            <div class="body-content">
                <div class="container">
                    <slot>
                        <div class="off-body-slot">
                            <div v-html="text"></div>
                        </div>
                    </slot>
                </div>
            </div>
            <div class="footer" :class="propBtnAlign">
                <dbutton
                    @click="confirmFn"
                    :type="state.cLevel"
                    :disabled="confirmDisabled"
                    v-show="confirmShow"
                >
                    
                </dbutton>
                <dbutton
                    @click="cancelFn"
                    border="none"
                    plgin="true"
                    :type="state.cLevel"
                    :disabled="cancelDisabled"
                    v-show="cancelShow"
                >
                    
                </dbutton>
            </div>
        </deck>
    </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, watch, onBeforeMount } from "vue";
import Deck from "../deck/deck.vue";
import DButton from "../button/button.vue";
import _ from "../../common/utils";
import { THEME_STYLE_E, HORIZONTAL_ALIGN_E } from "../../common/config";
import { dialogState, handleBtnState, handleFn } from "./innerdialog.data";

/**
 * dialog
 * v-modelopen
 * levelTHEME_STYLE_Edefaultprimary
 * title$refs.title
 * text$refs.default
 * btnAlignfooterleftcentermiddle
 * esc/escescclickbothclick
 * close-showconfirm-showcancel-show
 * close-disabledconfirm-disabledcancel-disabledtrue
 */
export default defineComponent({
    name: "innerdialog",
    components: { Deck, DButton },
    props: {
        modelValue: {
            type: [String, Boolean],
            default: false,
        },
        title: {
            type: String,
            default: "",
        },
        text: {
            type: String,
            default: "",
        },
        level: {
            type: String,
            default: THEME_STYLE_E.DEFAULT,
            validator(arg: string) {
                return _.some(
                    THEME_STYLE_E,
                    (type: string) => type === arg.toLowerCase()
                );
            },
        },
        closeShow: {
            type: [Boolean, String],
            default: true,
        },
        closeDisabled: {
            type: [Boolean, String],
            default: false,
        },
        confirmShow: {
            type: [Boolean, String],
            default: true,
        },
        confirmDisabled: {
            type: [Boolean, String],
            default: false,
        },
        cancelShow: {
            type: [Boolean, String],
            default: true,
        },
        cancelDisabled: {
            type: [Boolean, String],
            default: false,
        },
        btnAlign: {
            type: String,
            default: HORIZONTAL_ALIGN_E.CENTER,
            validator(arg: string) {
                return _.some(
                    HORIZONTAL_ALIGN_E,
                    (align: string) => align === arg.toLowerCase()
                );
            },
        },
        position: {
            type: String,
            default: "center",
        },
        move: {
            type: [String, Boolean],
            default: false,
        },
        esc: {
            type: String,
            default: "esc",
        },
    },
    methods: {
        // moveClickDown(e: Event) {
        //     if (this.dockerMove && this.openDialog) {
        //         this.moveState = 1;
        //         console.log("moveClickDown:", e);
        //     }
        // },
        // moveFn(e: Event) {
        //     if (
        //         this.dockerMove &&
        //         this.openDialog &&
        //         this.moveState === 1 &&
        //         this.refs.docker
        //     ) {
        //     }
        // },
        // moveClickUp(e: Event) {
        //     // this.moveState = 0;
        // },
        // resetPosition() {
        //     if (this.refs.docker) {
        //         this.refs.docker.style.margin = 0;
        //     }
        // },
    },
    // mounted() {
    //     this.$nextTick(() => {
    //         document.addEventListener("keydown", this.escKeypressFn);
    //     });

    //     this.bindListener();
    // },
    // beforeUnmount() {
    //     document.removeEventListener("keydown", this.escKeypressFn);
    // },
    setup(props: any, context: any) {
        let { state, handLevel, handleOpen, openPop, closePop } = dialogState();
        let { propBtnAlign, calcBtnAlign } = handleBtnState();
        let {
            closeFn,
            confirmFn,
            cancelFn,
            escClickFn,
            escKeypressFn,
        } = handleFn(props, context, state, closePop);

        let { modelValue, level, btnAlign } = toRefs(props);

        calcBtnAlign(propBtnAlign, btnAlign.value);
        handleOpen(modelValue);
        handLevel(level.value);

        watch(modelValue, handleOpen);
        watch(level, handLevel);

        // onBeforeMount(() => {
        //     calcBtnAlign(propBtnAlign, btnAlign.value);
        // });

        return {
            ...toRefs(props),
            state,
            openPop,
            closePop,
            propBtnAlign,
            calcBtnAlign,
            closeFn,
            confirmFn,
            cancelFn,
            escClickFn,
            escKeypressFn,
        };
    },
});
</script>