<template v-cloak>
    <button
        data-dbutton
        :auto-tips="tips"
        :disabled="disabled"
        :class="['dbutton', 'manual', type, propClass]"
        @click="defaultClickFn($event)"
    >
        <span class="content">
            <icon
                v-if="icons.iconLeft"
                :type="icons.iconLeft"
                :path="icons.iconLeftPath"
            ></icon>
            <span v-if="$slots.default" class="btn-slots"><slot></slot></span>
            <icon
                v-if="icons.iconRight"
                :type="icons.iconRight"
                :path="icons.iconRightPath"
            ></icon>
        </span>
    </button>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import _ from "../../common/utils";
import { THEME_STYLE_E } from "../../common/config";
import Icon from "../icon/icon.vue";

export default defineComponent({
    name: "dbutton",
    inheritAttrs: true,
    components: { Icon },
    props: {
        type: {
            type: String,
            default: THEME_STYLE_E.DEFAULT,
            validator(arg: string) {
                return _.some(THEME_STYLE_E, (type: string) => type === arg.toLowerCase());
            },
        },
        plgin: {
            type: [String, Boolean],
            default: false,
            required: false,
        },
        round: {
            type: [String, Boolean],
            default: false,
            required: false,
        },
        circle: {
            type: [String, Boolean],
            default: false,
            required: false,
        },
        border: {
            type: [String, Boolean],
            default: true,
            required: false,
        },
        icon: {
            type: [String, Object],
            required: false,
        },
        path: {
            type: String,
            required: false,
        },
        tips: {
            type: [String, Boolean],
            default: true,
            required: false,
        },
        disabled: {
            type: [String, Boolean],
            default: false,
            required: false,
        },
    },
    methods: {
        defaultClickFn() {
            this.$el.blur();
        }
    },
    computed: {
        icons() {
            let icons: any = {};
            let icon: any = this.icon;
            if (_.isString(icon)) {
                icons.iconLeft = icon;
            } else if (_.isObject(icon)) {
                icons.iconLeft = icon.iconLeft;
                icons.iconRight = icon.iconRight;
                icons.iconLeftPath = icon.iconLeftPath;
                icons.iconRightPath = icon.iconRightPath;
            } else if (_.isString(this.path)) {
                icons.iconLeftPath = this.path;
            }
            return icons;
        },
        propClass() {
            let propClass: any = {};
            propClass.round = this.round === true || this.round === "true";
            propClass.circle = this.circle === true || this.circle === "true";
            propClass.plgin = this.plgin === true || this.plgin === "true";
            propClass.disabled = this.disabled === true || this.disabled === "true";
            propClass["border-none"] = this.border !== true && this.border !== "true";

            return propClass;
        },
    },
});
</script>