import DButton from "./button/button.vue";
import Icon from "./icon/icon.vue";
import Deck from "./deck/deck.vue";
import InnerDialog from "./innerdialog/innerdialog.vue";
import Loading from "./loading/loading.vue";
import DInput from "./input/input.vue";
import DTextarea from "./textarea/textarea.vue";

import loadingService from "./loading/loadingService";
import messageService from "./innerdialog/messagebox";

import "../assets/style.scss";

export default function install(Vue: any) {
    Vue.component(DButton.name, DButton)
        .component(Icon.name, Icon)
        .component(Deck.name, Deck)
        .component(InnerDialog.name, InnerDialog)
        .component(Loading.name, Loading)
        .component(DInput.name, DInput)
        .component(DTextarea.name, DTextarea)
}

export {
    loadingService,
    messageService
}