import { reactive } from "vue";
import _ from "../../common/utils";

export let loadingState: Function = (props: any, context: any): object => {
    let maskId: string = `mask_${parseInt(
        (Math.random() * 100000000).toString()
    ).toString()}-${new Date().getTime().toString()}`;

    if (!_.isEmpty(props.name) && _.isString(props.name)) {
        maskId = props.name;
    }

    let maskUrl: string = `url(#${maskId})`;

    let state = reactive({
        openDialog: false,
        closeDialog: false,
        inited: false,
    });

    let openPop: Function = (): void => {
        state.openDialog = true;
        state.closeDialog = false;
    };

    let closePop: Function = (cbk: Function): void => {
        state.openDialog = false;
        state.closeDialog = true;
        setTimeout(() => {
            cbk();
        }, 150);
    };

    return {
        maskId,
        maskUrl,
        state,
        openPop,
        closePop,
    };
};

export default {
    loadingState
}