import { h, defineComponent, render, VNode } from "vue";
import { LoadingInterface, LoadingAttrs } from "../../common/interface";
import _ from '../../common/utils';
import Loading from './loading.vue';

const loadingConstructor = defineComponent(Loading);

let LIArr: LoadingInterface[] = [];

let loadingService = {
    _createLoadingBox(options: any): VNode {
        return h(loadingConstructor, options);
    },
    _onMount(parentNode: HTMLElement, mountNode: HTMLDivElement, vnode: VNode) {
        render(vnode, mountNode);
        parentNode.appendChild(mountNode);
    },
    _onUnmount(parentNode: HTMLElement, mountNode: HTMLDivElement,) {
        render(null, <HTMLDivElement>mountNode);
        parentNode!.removeChild(<HTMLDivElement>mountNode);
    },
    show(parentId: string | undefined = undefined): LoadingInterface {
        let attrs: LoadingAttrs = {
            show: true,
            global: parentId ? false : true,
            closeCbk() {
                loadingService._onUnmount(_parentNode!, _mountNode!);
            }
        };

        let loadingBox: LoadingInterface = {
            close() { }
        };
        const loadingVNode: VNode = this._createLoadingBox(attrs);

        let _mountNode: HTMLDivElement | null = document.createElement("div");
        let _parentNode: HTMLElement | null = document.getElementById(parentId!);

        if (parentId) {
            _parentNode = document.getElementById(parentId);
        } else {
            _parentNode = document.body;
        }

        if (!_parentNode) {
            console.error(`can not find element by id: ${parentId}`);
            return loadingBox;
        }

        this._onMount(_parentNode, _mountNode, loadingVNode);

        loadingBox = {
            close() {
                if (loadingVNode.component && loadingVNode.component.vnode.component) {
                    loadingVNode.component.vnode.component.props.show = false;
                    LIArr.some((box, i, arr) => {
                        if (box === loadingBox) {
                            arr.splice(i, 1);
                        }
                    });
                }
            }
        };

        LIArr.push(loadingBox);

        return loadingBox;
    },
    close() {
        while (LIArr.length > 0) {
            let LI = LIArr.shift();
            LI && _.isFunction(LI.close) && LI.close();
        };
    }
};

Object.defineProperties(loadingService, {
    _createLoadingBox: {
        writable: false,
        enumerable: false
    },
    _onMount: {
        writable: false,
        enumerable: false
    },
    _onUnmount: {
        writable: false,
        enumerable: false
    },
});


export default loadingService;