import { ref } from "vue";
import { SVG_ICONS } from "../../common/config";

export let getIconData: Function = (): object => {
    let iconPath = ref();

    let getPath: Function = (type: string, path: string): string => SVG_ICONS[type.toUpperCase()] || path;

    return {
        iconPath,
        getPath
    };
};

export default {
    getIconData
}