// test

import { h, defineComponent, VNode, render } from "vue";
import InnerDialog from "./innerdialog.vue";

const innerDialogConstructor = defineComponent(InnerDialog);

let messageService =  {
    _createBox(options: any, content: any): VNode {
        return h(innerDialogConstructor, options, content);
    },
    _onMount(parentNode: HTMLElement, mountNode: HTMLDivElement, vnode: VNode) {
        render(vnode, mountNode);
        parentNode.appendChild(mountNode);
    },
    _onUnmount(parentNode: HTMLElement, mountNode: HTMLDivElement,) {
        render(null, <HTMLDivElement>mountNode);
        parentNode!.removeChild(<HTMLDivElement>mountNode);
    },
    _showBox(msg: any, type: string, confirmFn?: Function, cancelFn?: Function, closeFn?: Function) {
        const dialogVNode: VNode = this._createBox({
            innertext: msg,
            title: type,
        }, {
            confirm: confirmFn,
            cancel: cancelFn,
            close: closeFn,
        });

        let _mountNode: HTMLDivElement = document.createElement("div");
        let _parentNode: HTMLElement = document.body;

        this._onMount(_parentNode, _mountNode, dialogVNode);

        let self = this;

        let msgBox: object = {
            close() {
                self._onUnmount(_parentNode!, _mountNode!);
            }
        }

        return msgBox;
    },
    info(msg: any, confirmFn?: Function, cancelFn?: Function, closeFn?: Function) {
        return this._showBox(msg, "info", confirmFn, cancelFn, closeFn);
    }

}

Object.defineProperties(messageService, {
    _createBox: {
        writable: false,
        enumerable: false
    },
    _onMount: {
        writable: false,
        enumerable: false
    },
    _onUnmount: {
        writable: false,
        enumerable: false
    },
    _showBox: {
        writable: false,
        enumerable: false
    },
});

export default messageService;