<template>
    <textarea
        :id="$attrs.id + '_textarea'"
        spellcheck="false"
        :class="propClass"
        :style="propStyle"
        :disabled="state.disabled"
        :placeholder="$attrs.placeholder"
        :value="modelValue"
        @focus="onFocus"
    ></textarea>
</template>

<script lang="ts">
import { defineComponent, inject, toRefs } from "vue";
import { textareaState } from "./textarea.data";
import { THEME_STYLE_E } from "../../common/config";
import duiService from "../../services/duiService";

export default defineComponent({
    name: "dtextarea",
    components: {},
    data() {
        return {};
    },
    emits: ["change", "update:modelValue", "focus", "blur"],
    props: {
        modelValue: {
            type: String,
            default: "",
        },
        resize: {
            type: [String, Boolean],
            default: true,
        },
        disabled: {
            type: [String, Boolean],
            default: false,
        },
        validate: {
            type: Function,
        },
        realTime: {
            type: [String, Boolean],
            default: false,
        },
        width: {
            type: String,
            default: "200",
        },
        height: {
            type: String,
            default: "65",
        },
        length: {
            type: [String, Number],
            default: 0,
        },
    },
    computed: {
        propClass(): string[] {
            let propClass: string[] = [];

            propClass.push("dtextarea");
            this.state.cLevel = THEME_STYLE_E.PRIMARY;

            if (this.disabled === true || this.disabled === "true") {
                this.state.disabled = true;
                propClass.push("disabled");
            } else {
                this.state.disabled = false;
            }

            if (this.state.error && !this.state.disabled) {
                propClass.push("error");
                this.state.cLevel = THEME_STYLE_E.DANGER;
            }

            if (this.state.focused) {
                propClass.push("focused");
            }

            if (this.resize === false || this.resize === "false") {
                propClass.push("resizenone");
            }

            return propClass;
        },
        propStyle(): object {
            let propStyle: {
                [key: string]: string;
            } = {};

            propStyle.width = duiService.getSizeWithSimer(this.width);
            propStyle.height = duiService.getSizeWithSimer(this.height);

            return propStyle;
        },
    },
    methods: {
        onFocus(e: FocusEvent) {
            this.state.focused = true;
        },
    },
    watch: {},
    setup(props: any) {
        let { state } = textareaState(props);

        const MITT_EVENT = inject("MITT_EVENT") || "";

        return {
            ...toRefs(props),
            MITT_EVENT,
            state,
        };
    },
});
</script>