
import { defineComponent, toRefs, inject, watch } from "vue";
import { inputState } from "../input/input.data";
import Icon from "../icon/icon.vue";
import Deck from "../deck/deck.vue";
import _ from "../../common/utils";
import {
    INPUT_TYPE_E,
    INPUT_TYPE_COVER_E,
    DUI_EVENTS_E,
    THEME_STYLE_E,
} from "../../common/config";
import eventService from "../../services/eventService";

/**
 * input
 * v-model
 * typetextpassword
 * width
 * disabled
 * validate:
 * history
 * realTimefalse
 * changechangenewVal/oldVal
 * animation
 */
export default defineComponent({
    name: "dinput",
    inheritAttrs: false,
    components: { Icon, Deck },
    emits: ["change", "update:modelValue", "focus", "blur"],
    data() {
        return {
            inputHistory: <string[]>[],
        };
    },
    props: {
        modelValue: {
            type: String,
            default: "",
        },
        name: {
            type: String,
        },
        type: {
            type: String,
            default: INPUT_TYPE_E.TEXT,
        },
        digist: {
            type: [String, Number],
            default: 0,
        },
        width: {
            type: String,
            default: "150px",
        },
        history: {
            type: [String, Boolean],
            default: true,
        },
        animation: {
            type: [String, Boolean],
            default: true,
        },
        disabled: {
            type: [String, Boolean],
            default: false,
        },
        validate: {
            type: Function,
        },
        realTime: {
            type: [String, Boolean],
            default: false,
        },
    },
    computed: {
        typeIn(): string {
            if (this.type.toLowerCase() === INPUT_TYPE_E.PASSWORD) {
                if (this.state.viewable) {
                    return INPUT_TYPE_E.TEXT;
                }

                return INPUT_TYPE_E.PASSWORD;
            }

            return INPUT_TYPE_E[this.type.toUpperCase()] || INPUT_TYPE_E.TEXT;
        },
        historyIn(): boolean {
            return (
                (this.history === "true" || this.history === true) &&
                !_.some(["password", "number"], (type: string) => {
                    return this.type.toLowerCase() === type;
                }) &&
                !_.isEmpty(this.name)
            );
        },
        propClass() {
            let propClass: string[] = [];

            propClass.push("dinput");
            this.state.cLevel = THEME_STYLE_E.PRIMARY;

            if (this.disabled === true || this.disabled === "true") {
                this.state.disabled = true;
                propClass.push("disabled");
            } else {
                this.state.disabled = false;
            }

            if (this.animation === true || this.animation === "true") {
                propClass.push("animation");
            }

            if (this.type.toLowerCase() === INPUT_TYPE_E.PASSWORD) {
                propClass.push("password");
            }

            if (this.state.error && !this.state.disabled) {
                propClass.push("error");
                this.state.cLevel = THEME_STYLE_E.DANGER;
            }

            return propClass;
        },
        historyList(): any[] {
            let res = [];

            if (_.isEmpty(this.modelValue)) {
                res = this.inputHistory;
            } else {
                res = this.inputHistory.filter(
                    (data: string | number) =>
                        (<string>data).indexOf(this.modelValue) > -1
                );
            }

            return res;
        },
    },
    methods: {
        onFocus(e: FocusEvent) {
            this.state.onFocus = true;
            this.state.focused = true;
            this.state.error = false;
            // this.$emit("focus", e);

            if (this.historyIn && this.historyList.length > 0) {
                this.showHistoryFn(
                    e,
                    this.$refs.inputBody,
                    this.historyList.length
                );
            }
        },
        onBlur() {
            let val: string;
            this.state.onFocus = false;

            if (this.type.toLowerCase() === INPUT_TYPE_COVER_E.NUMBER) {
                val = _.floor(this.modelValue, this.digist, false);
                (<HTMLInputElement>this.$refs.input).value = val;
                this.$emit("update:modelValue", val);
            }



            if (!_.isEmpty(this.MITT_EVENT)) {
                eventService.emit(
                    DUI_EVENTS_E.DINPUT_VALIDATE + this.MITT_EVENT
                );
            } else {
                this.onValidate();
            }

            if (this.historyIn) {
                this.saveInput();
            }

            // this.$emit("blur", arguments);
        },
        onValidate(newVal?: string) {
            let val = !_.isUndefined(newVal) ? newVal : this.modelValue;

            if (this.state.disabled) {
                return;
            }

            if (_.isFunction(this.validate) && !this.validate!(val)) {
                this.state.error = true;
            } else {
                this.state.error = false;
            }
        },
        onInput(e: { target: HTMLInputElement }) {
            let val: string = e.target.value;
            let oldVal = this.modelValue;

            if (this.type.toLowerCase() === INPUT_TYPE_COVER_E.NUMBER) {
                val = _.floor(e.target.value, this.digist, true);
            }

            (<HTMLInputElement>this.$refs.input).value = val;

            this.$emit("update:modelValue", val);
            if (val !== oldVal) {
                if (this.type.toLowerCase() !== INPUT_TYPE_COVER_E.NUMBER || val.substr(val.length - 1) !== '.') {
                    this.$emit("change", val, oldVal);
                }
            }

            if (this.realTime === true || this.realTime === "true") {
                return this.onValidate(val);
            }

            this.state.error = false;
        },
        saveInput() {
            if (!_.isEmpty(this.modelValue)) {
                this.inputHistory.push(this.modelValue);
            }

            this.inputHistory = _.unique(this.inputHistory);

            localStorage.setItem(this.name!, JSON.stringify(this.inputHistory));
        },
        choseData(e: MouseEvent, val: string): void {
            let oldVal = this.modelValue;

            this.$emit("update:modelValue", val);
            if (val !== oldVal) {
                this.$emit("change", val, oldVal);
            }
            eventService.emit(DUI_EVENTS_E.DINPUT_VALIDATE + this.MITT_EVENT);
            this.onValidate(val);
            this.closeHistoryFn();
        },
        removeHistory(e: MouseEvent, data: string) {
            this.inputHistory = this.inputHistory.filter(
                (val: string) => val !== data
            );

            if (this.inputHistory.length === 0) {
                this.closeHistoryFn();
                // <HTMLInputElement>(this.$refs.input).focus();
            }

            localStorage.setItem(this.name!, JSON.stringify(this.inputHistory));
        },
        getInputHistory() {
            this.inputHistory =
                JSON.parse(localStorage.getItem(this.name!)!) || [];
        },
        inputEmitHandler(newVal?: string, oldVal?: string) {
            if (this.state.focused) {
                this.onValidate(newVal);
            }
        },
    },
    created() {
        if (!_.isEmpty(this.MITT_EVENT)) {
            eventService.on(
                DUI_EVENTS_E.DINPUT_VALIDATE + this.MITT_EVENT,
                this.inputEmitHandler
            );
        }

        if (this.historyIn) {
            this.getInputHistory();
        }
    },
    mounted() {
        this.onValidate();
    },
    beforeUnmount() {
        if (!_.isEmpty(this.MITT_EVENT)) {
            eventService.off(
                DUI_EVENTS_E.DINPUT_VALIDATE + this.MITT_EVENT,
                this.inputEmitHandler
            );
        }
    },
    setup(props: any) {
        let {
            state,
            showHistoryFn,
            closeHistoryFn,
            mouseoverFn,
            mouseleaveFn,
            iconClickFn,
        } = inputState(props);

        const MITT_EVENT = inject("MITT_EVENT") || "";

        watch(
            () => [state.onFocus, state.onOver],
            ([focus, over]) => {
                if (!focus && !over) {
                    closeHistoryFn();
                }
            }
        );

        return {
            ...toRefs(props),
            INPUT_TYPE_E,
            MITT_EVENT,
            state,
            showHistoryFn,
            closeHistoryFn,
            mouseoverFn,
            mouseleaveFn,
            iconClickFn,
        };
    },
});
