// interface someCbk {
//     Function(<T>, string | number, any): boolean
// }

/**
 * 
 */
export default {
    isUndefined(arg: any): boolean {
        return typeof arg === 'undefined';
    },
    isFunction(arg: any): boolean {
        return typeof arg === 'function';
    },
    isArray(arg: any): boolean {
        return this.isFunction(Array.isArray) ?
            Array.isArray(arg) : Object.prototype.toString.call(arg) === '[object Array]';
    },
    isNull(arg: any): boolean {
        return arg === null;
    },
    isNaN(arg: any): boolean {
        return isNaN(arg);
    },
    isEmpty(arg: any): boolean {
        if (this.isObject(arg)) {
            return Object.keys(arg).length === 0;
        } else if (this.isArray(arg) || this.isString(arg)) {
            return arg.length === 0;
        } else {
            return this.isUndefined(arg) || this.isNull(arg) || this.isNaN(arg);
        }
    },
    isNumber(arg: any): boolean {
        return typeof arg === 'number';
    },
    isString(arg: any): boolean {
        return typeof arg === 'string';
    },
    isObject(arg: any): boolean {
        return Object.prototype.toString.call(arg) === '[object Object]';
    },
    /**
     * 
     * @param classList Array 
     * @param classes String|Array 
     *        String,|'+', ,'+'.'+'
     *        Array,
     * @return Boolean
     */
    has(classList: [], classes: any): boolean {
        let list1: string[] = [];
        let list2: any[] = [];

        if (!this.isArray(classList)) {
            return false;
        }

        if (this.isArray(classes)) {
            return classList.some((cls: string) => classes.some((c: string) => c === cls));
        } else if (this.isString(classes)) {
            list1 = classes.replace(/\s*\+\s*/g, '+').split(/\s+/);

            list1.forEach((cls: string) => {
                let currentList: string[] = cls.split(/\+/);

                list2.push(currentList.every(cl => {
                    return classList.some(c => c === cl);
                }))
            });

            return list2.some(c => c);
        } else {
            return false;
        }
    },
    /**
     * some
     */
    some(list: any, callback: Function): boolean {
        if (this.isArray(list)) {
            return list.some(callback);
        } else if (this.isObject(list) || this.isString(list)) {
            return Object.keys(list).some((key) => {
                return callback(list[key], key, list);
            });
        }

        return false;
    },

    /**
     * 
     */
    unique<T>(arr: any[]): any[] {
        if (!this.isArray(arr)) {
            console.log('type error!');
            return [];
        }

        if (Set) {
            return [...new Set(arr)];
        } else if (this.isFunction(arr.filter)) {
            return arr.filter((item, index, arr) => {
                //==
                return arr.indexOf(item, 0) === index;
            });
        } else {
            let arrry: T[] = [];
            let obj: object = {};
            let i: number = 0;
            for (; i < arr.length; i++) {
                if (!obj[arr[i]]) {
                    arrry.push(arr[i]);
                    obj[arr[i]] = 1;
                } else {
                    obj[arr[i]]++;
                }
            }
            return arrry;
        }
    },

    /**
     * N
     * keepPoint
     */
    floor(num: string | number, digist: number | string = 0, keepPoint: boolean = false): string {
        let res: string = '';
        let hasSimer = num.toString().indexOf('.') > -1;
        let resArr: string[] = num.toString().split('.');

        if (this.isNaN(parseInt(resArr[0]))) {
            return res; // string
        } else {
            resArr[0] = parseInt(resArr[0]).toString();
        }

        // 
        if (!this.isUndefined(resArr[1])) {
            if (this.isNaN(parseInt(resArr[1]))) {
                resArr[1] = '';
            } else {
                resArr[1] = parseInt(resArr[1]).toString().slice(0, <number>digist);
            }
        }

        if (+digist === 0) {
            // 
            res = resArr[0];
        } else if (this.isEmpty(resArr[1])) {
            // 
            res = keepPoint && hasSimer ? resArr[0] + '.' : resArr[0];
        } else {
            res = resArr[0] + '.' + resArr[1];
        }

        return res;
    }
}