
import { defineComponent, toRefs, watch } from "vue";
import _ from "../../common/utils";
import { getIconData } from "../icon/icon.data";
/**
 * icon
 * size/(widthheight)iconsize - 16widthheight,pxrem
 * typeiconSVG_ICONS
 * pathtypetypepath
 */
export default defineComponent({
    name: "icon",
    components: {},
    props: {
        size: {
            type: [String, Number],
            default: "16",
        },
        width: {
            type: [String, Number],
        },
        height: {
            type: [String, Number],
        },
        type: {
            type: String,
            default: "",
        },
        path: {
            type: String,
            default: "",
        },
    },
    computed: {
        isShow(): boolean {
            return !_.isEmpty(this.iconPath);
        },
        propClass(): any {
            let classes = [];

            if (!_.isEmpty(this.type)) {
                classes.push(`icon-${this.type}`);
            }

            return classes;
        },
        widthIn(): number | string {
            return this.width || this.size;
        },
        heightIn(): number | string {
            return this.height || this.size;
        },
    },
    setup(props: any) {
        let { iconPath, getPath } = getIconData();

        let { type, path } = toRefs(props);

        iconPath.value = getPath(type.value, path.value);
        watch(type, (newVal) => {
            iconPath.value = getPath(newVal, path.value);
        });
        watch(path, (newVal) => {
            iconPath.value = getPath(type.value, newVal);
        });

        return {
            ...toRefs(props),
            iconPath,
        };
    },
});
