<template v-cloak>
    <div class="DialogModule">
        <div class="row toolarea center">
            <dbutton @click="showDialog">showDialog</dbutton>
            <dinput
                :width="width"
                v-model="inputVal"
                :validate="validateFn"
                @focus="onFocus"
                @blur="onBlur"
            ></dinput>
        </div>
        <innerdialog
            move="true"
            close-show="false"
            :confirm-disabled="cd"
            esc="both"
            :level="level"
            v-model="open"
            @close="closeFn"
            title="alarm: "
            text="asdfas<br><br><br><br><br><br><br>"
        >
        </innerdialog>
    </div>
</template>

<script lang="ts">
import { defineComponent, provide } from "vue";
import InnerDialog from "@/components/innerdialog/innerdialog.vue";

export default defineComponent({
    name: "DialogModule",
    components: {
        InnerDialog,
    },
    data() {
        return {
            open: false,
            cd: true,
            level: "danger",
            inputVal: ",come on!!",
            width: <string><unknown>undefined,
        };
    },
    computed: {},
    methods: {
        onFocus() {
            this.width = "200px";
        },
        onBlur() {
            this.width = "150px";
        },
        showDialog() {
            this.open = true;
            setTimeout(() => {
                // this.$set(this.level, "danger")

                // this.changeLevel();
                this.level = "info";
                // this.$nextTick(() => {
                // });
            }, 1000);
        },
        closeFn(e: Event, close: Function) {
            this.open = !window.confirm("");

            this.level = "info";
            // close();
        },
        validateFn(newVal: string) {
            return newVal.length > 90;
        },
    },
    mounted() {
        setInterval(() => {
            this.cd = !this.cd;
        }, 1500);
    },
    watch: {},
    setup() {
        // let { changeLevel, resetLevel } = dialogData;

        provide("MITT_EVENT", "DialogModule");

        return {};
    },
});
</script>

