import { reactive } from "vue";
import _ from "../../common/utils";
import { HORIZONTAL_ALIGN_E, THEME_STYLE_E } from "../../common/config";

let dialogState = () => {
    let primary: string = <string>THEME_STYLE_E.PRIMARY;

    let state = reactive({
        cLevel: primary,
        openDialog: false,
        closeDialog: false,
    });

    let handleOpen = (newVal: string | boolean = false) => {
        if (newVal === true || newVal === "true") {
            openPop();
        } else {
            closePop();
        }
    };

    let handLevel = (level: string = primary) => {
        if (level && _.some(THEME_STYLE_E, (type: string) => type === level.toLowerCase())) {
            return state.cLevel = level === <string>THEME_STYLE_E.DEFAULT ? primary : level;
        }

        state.cLevel = primary;
    };

    let hand = (level: string = primary) => {
        if (level && _.some(THEME_STYLE_E, (type: string) => type === level.toLowerCase())) {
            return state.cLevel = level === <string>THEME_STYLE_E.DEFAULT ? primary : level;
        }

        state.cLevel = primary;
    };

    let openPop = () => {
        state.openDialog = true;
        state.closeDialog = false;
    };

    let closePop = (cb?: Function) => {
        state.openDialog = false;
        state.closeDialog = true;
        setTimeout(() => {
            state.closeDialog = false;

            if (_.isFunction(cb)) {
                cb!();
            }
        }, 150);
    }

    return {
        state,
        openPop,
        closePop,
        handleOpen,
        handLevel
    }
};

let handleBtnState = (props?: any) => {
    let propBtnAlign = reactive({
        "align-left": false,
        "align-center": false,
        "align-right": false,
    });

    let resetBtnAlign = (propClass: any) => {
        propClass["align-left"] = false;
        propClass["align-center"] = false;
        propClass["align-right"] = false;
    };

    let calcBtnAlign = (btnAlign: any, align: string = HORIZONTAL_ALIGN_E.CENTER) => {
        resetBtnAlign(btnAlign);
        btnAlign[`align-${align}`] = true;
    };

    return {
        propBtnAlign,
        resetBtnAlign,
        calcBtnAlign
    };
};

let handleFn = (props: any, context: any, state: any, closePop: Function) => {
    let handleAttrFun = (e: Event, eNames: string[]) => {
        let hasFn = _.some(eNames, (eName: string) => {
            if (_.isFunction(context.attrs[eName])) {
                context.emit(eName.replace(/on([A-Z])/, (mv: string, $v: string) => $v.toLowerCase()), e, closePop);
                return true;
            }
            return false;
        });

        if (!hasFn) {
            context.emit("update:modelValue", false);
        }
    }
    let closeFn = (e: Event) => {
        handleAttrFun(e, ["onClose", "onCancel"]);
    }
    let confirmFn = (e: Event) => {
        handleAttrFun(e, ["onConfirm"]);
    }
    let cancelFn = (e: Event) => {
        handleAttrFun(e, ["onCancel", "onClose"]);
    }
    let escClickFn = (e: Event) => {
        if (
            (props.esc === "click" || props.esc === "both") &&
            state.openDialog === true
        ) {
            cancelFn(e);
        }
    }
    let escKeypressFn = (e: KeyboardEvent) => {
        if (
            (props.esc === "esc" || props.esc === "both") &&
            state.openDialog === true
        ) {
            cancelFn(e);
        }
    }

    return {
        closeFn,
        confirmFn,
        cancelFn,
        escClickFn,
        escKeypressFn,
    }
};

// let handPosition = () => { };

export {
    dialogState,
    handleBtnState,
    handleFn,
    // handPosition
}