import { position } from "../common/interface";

export default {
    /**
     * 
     */
    getElementViewPosition(e: Event): position {
        let element: HTMLElement = <HTMLElement>e.target;
        //x
        let actualLeft: number = element.offsetLeft;
        let currentLeft: any = element.offsetParent;
        while (currentLeft !== null) {
            actualLeft += (currentLeft.offsetLeft + currentLeft.clientLeft);
            currentLeft = currentLeft.offsetParent;
        }

        let eScrollLeft: number;
        let left: number;

        if (document.compatMode == "BackCompat") {
            eScrollLeft = document.body.scrollLeft;
        } else {
            eScrollLeft = document.documentElement.scrollLeft;
        }
        left = actualLeft - eScrollLeft;

        //y
        let actualTop: number = element.offsetTop;
        let currentTop: any = element.offsetParent;
        while (currentTop !== null) {
            actualTop += (currentTop.offsetTop + currentTop.clientTop);
            currentTop = currentTop.offsetParent;
        }

        let eScrollTop: number;
        let right: number;

        if (document.compatMode == "BackCompat") {
            eScrollTop = document.body.scrollTop;
        } else {
            eScrollTop = document.documentElement.scrollTop;
        }
        right = actualTop - eScrollTop;

        //
        return { x: left, y: right };
    },
    /**
     * 
     */
    getElementPagePosition(e: Event): position {
        let element: HTMLElement = <HTMLElement>e.target;
        //x
        let actualLeft: number = element.offsetLeft;
        let currentLeft: any = element.offsetParent;
        while (currentLeft !== null) {
            actualLeft += currentLeft.offsetLeft;
            currentLeft = currentLeft.offsetParent;
        }
        //y
        let actualTop: number = element.offsetTop;
        let currentTop: any = element.offsetParent;
        while (currentTop !== null) {
            actualTop += (currentTop.offsetTop + currentTop.clientTop);
            currentTop = currentTop.offsetParent;
        }
        //
        return { x: actualLeft, y: actualTop };
    },
    /**
     * 
     */
    getElementSize(element: HTMLElement): position {
        let width = element.offsetWidth || element.clientWidth
        let height = element.offsetHeight || element.clientHeight
        //
        return { x: width, y: height };
    },
    /**
     * px
     */
    getSizeWithSimer(size: string | number): string {
        size = <string>size;
        let resSize: string = "";
        let matchRes = size.match(/^[[0-9.]+](px|rem|em|vw|vh|vmin|vmcx|ex|ch|%|cm|mm|in|pt|pc)&/i);

        if (/^[0-9\.\-]+$/i.test(size)) {
            resSize = <string><unknown>(parseInt(size)) + "px";
        } else if (matchRes) {
            resSize = <string><unknown>parseFloat(size) + matchRes[0];
        }

        //
        return resSize;
    },
}